const priorityConfig = {
  low: { 
    color: 'bg-emerald-100 text-emerald-700 border-emerald-200 hover:bg-emerald-200',
    icon: CircleIcon,
    gradient: 'from-emerald-400 to-teal-500'
  },
  medium: { 
    color: 'bg-amber-100 text-amber-700 border-amber-200 hover:bg-amber-200',
    icon: ClockIcon,
    gradient: 'from-amber-400 to-orange-500'
  },
  high: { 
    color: 'bg-rose-100 text-rose-700 border-rose-200 hover:bg-rose-200',
    icon: StarIcon,
    gradient: 'from-rose-400 to-pink-500'
  }
};

export default function TaskManager() {
  const [darkMode, setDarkMode] = useState(false);
  const [tasks, setTasks] = useState<Task[]>([]);
  const [newTask, setNewTask] = useState({ 
    title: '', 
    description: '', 
    priority: 'medium' as Task['priority'],
    dueDate: ''
  });
  const [editingTask, setEditingTask] = useState<Task | null>(null);
  const [filter, setFilter] = useState<'all' | 'pending' | 'completed' | 'overdue'>('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [draggedTaskId, setDraggedTaskId] = useState<string | null>(null);
  const [dragOverTaskId, setDragOverTaskId] = useState<string | null>(null);

  useEffect(() => {
    const savedTasks = localStorage.getItem('tasks');
    const savedDarkMode = localStorage.getItem('darkMode') === 'true';
    
    if (savedTasks) {
      try {
        setTasks(JSON.parse(savedTasks));
      } catch (e) {
        console.error('Failed to parse tasks from localStorage');
      }
    }
    
    setDarkMode(savedDarkMode);
  }, []);

  useEffect(() => {
    localStorage.setItem('tasks', JSON.stringify(tasks));
    localStorage.setItem('darkMode', darkMode.toString());
  }, [tasks, darkMode]);

  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  };

  const addTask = () => {
    if (newTask.title.trim() === '') return;
    
    const task: Task = {
      id: Date.now().toString(),
      title: newTask.title,
      description: newTask.description,
      completed: false,
      createdAt: new Date().toISOString(),
      priority: newTask.priority,
      dueDate: newTask.dueDate || undefined
    };
    
    setTasks([task, ...tasks]);
    setNewTask({ title: '', description: '', priority: 'medium', dueDate: '' });
  };

  const updateTask = (id: string, updates: Partial<Task>) => {
    setTasks(tasks.map(task => 
      task.id === id ? { ...task, ...updates } : task
    ));
  };

  const deleteTask = (id: string) => {
    setTasks(tasks.filter(task => task.id !== id));
  };

  const toggleComplete = (id: string) => {
    const task = tasks.find(t => t.id === id);
    if (task) {
      updateTask(id, { completed: !task.completed });
    }
  };

  const startEdit = (task: Task) => {
    setEditingTask({ ...task });
  };

  const saveEdit = () => {
    if (!editingTask) return;
    updateTask(editingTask.id, editingTask);
    setEditingTask(null);
  };

  const cancelEdit = () => {
    setEditingTask(null);
  };

  const handleDragStart = (e: React.DragEvent<HTMLDivElement>, id: string) => {
    e.dataTransfer.setData("text/plain", id);
    setDraggedTaskId(id);
  };

  const handleDragOver = (e: React.DragEvent<HTMLDivElement>, id: string) => {
    e.preventDefault();
    if (id !== dragOverTaskId) {
      setDragOverTaskId(id);
    }
  };

  const handleDragLeave = () => {
    setDragOverTaskId(null);
  };

  const handleDrop = (e: React.DragEvent<HTMLDivElement>, targetId: string) => {
    e.preventDefault();
    const draggedId = e.dataTransfer.getData("text/plain");
    
    if (draggedId === targetId) {
      setDragOverTaskId(null);
      return;
    }

    const draggedIndex = tasks.findIndex(task => task.id === draggedId);
    const targetIndex = tasks.findIndex(task => task.id === targetId);

    if (draggedIndex === -1 || targetIndex === -1) return;

    const newTasks = [...tasks];
    const [draggedTask] = newTasks.splice(draggedIndex, 1);
    newTasks.splice(targetIndex, 0, draggedTask);
    
    setTasks(newTasks);
    setDragOverTaskId(null);
    setDraggedTaskId(null);
  };

  const handleDragEnd = () => {
    setDraggedTaskId(null);
    setDragOverTaskId(null);
  };

  const filteredTasks = tasks.filter(task => {
    const isOverdue = task.dueDate && new Date(task.dueDate) < new Date() && !task.completed;
    
    const matchesFilter = filter === 'all' || 
      (filter === 'pending' && !task.completed && !isOverdue) ||
      (filter === 'completed' && task.completed) ||
      (filter === 'overdue' && isOverdue);
    
    const matchesSearch = task.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      task.description.toLowerCase().includes(searchTerm.toLowerCase());
    
    return matchesFilter && matchesSearch;
  });

  const completedCount = tasks.filter(task => task.completed).length;
  const overdueCount = tasks.filter(task => 
    task.dueDate && new Date(task.dueDate) < new Date() && !task.completed
  ).length;
  const pendingCount = tasks.length - completedCount - overdueCount;
  const totalCount = tasks.length;
  const progressPercentage = totalCount > 0 ? (completedCount / totalCount) * 100 : 0;

  return (
    <div className={`min-h-screen relative overflow-hidden transition-colors duration-300 ${
      darkMode 
        ? 'bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 text-gray-200' 
        : 'bg-gradient-to-br from-slate-50 via-blue-50 to-indigo-100 text-gray-800'
    }`}>
      {/* Background decorative elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className={`absolute -top-40 -right-40 w-80 h-80 rounded-full blur-3xl ${
          darkMode 
            ? 'bg-gradient-to-br from-blue-800/10 to-purple-900/10' 
            : 'bg-gradient-to-br from-blue-400/20 to-purple-600/20'
        }`}></div>
        <div className={`absolute -bottom-40 -left-40 w-80 h-80 rounded-full blur-3xl ${
          darkMode 
            ? 'bg-gradient-to-tr from-emerald-800/10 to-blue-900/10' 
            : 'bg-gradient-to-tr from-emerald-400/20 to-blue-600/20'
        }`}></div>
        <div className={`absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-96 h-96 rounded-full blur-3xl ${
          darkMode 
            ? 'bg-gradient-to-r from-purple-900/10 to-pink-900/10' 
            : 'bg-gradient-to-r from-purple-400/10 to-pink-600/10'
        }`}></div>
      </div>

      {/* Floating drag indicator */}
      {draggedTaskId && (
        <div className={`fixed top-4 left-1/2 transform -translate-x-1/2 z-50 px-6 py-3 rounded-full shadow-lg flex items-center gap-3 ${
          darkMode 
            ? 'bg-gradient-to-r from-blue-700 to-purple-700 text-white' 
            : 'bg-gradient-to-r from-blue-600 to-purple-600 text-white'
        }`}>
          <DragHandleIcon className="h-5 w-5 animate-pulse" />
          <span>Drag task to reorder</span>
          <DragHandleIcon className="h-5 w-5 animate-pulse" />
        </div>
      )}

      <div className="relative z-10 container mx-auto px-4 py-8 max-w-5xl">
        {/* Dark mode toggle button */}
        <div className="absolute top-6 right-6">
          <button
            onClick={toggleDarkMode}
            className={`p-2 rounded-full transition-all duration-300 ${
              darkMode 
                ? 'bg-gray-700 hover:bg-gray-600 text-yellow-300' 
                : 'bg-gray-200 hover:bg-gray-300 text-blue-900'
            }`}
            aria-label={darkMode ? "Switch to light mode" : "Switch to dark mode"}
          >
            {darkMode ? (
              <SunIcon className="h-6 w-6" />
            ) : (
              <MoonIcon className="h-6 w-6" />
            )}
          </button>
        </div>

        {/* Header */}
        <div className="text-center mb-12">
          <div className={`inline-flex items-center justify-center w-16 h-16 rounded-2xl mb-6 shadow-lg ${
            darkMode 
              ? 'bg-gradient-to-r from-blue-700 to-purple-700' 
              : 'bg-gradient-to-r from-blue-600 to-purple-600'
          }`}>
            <CheckCircleIcon className="h-8 w-8 text-white" />
          </div>
          <h1 className={`text-5xl font-bold mb-4 bg-clip-text text-transparent ${
            darkMode 
              ? 'bg-gradient-to-r from-gray-300 via-blue-300 to-purple-300' 
              : 'bg-gradient-to-r from-gray-900 via-blue-800 to-purple-800'
          }`}>
            Task Manager
          </h1>
          <p className={`text-xl mb-8 max-w-2xl mx-auto leading-relaxed ${
            darkMode ? 'text-gray-400' : 'text-gray-600'
          }`}>
            Transform your productivity with our beautifully designed task management system
          </p>
          
          {/* Progress Stats and Circular Chart */}
          <div className="flex flex-col lg:flex-row items-center justify-center gap-8">
            {/* Left: Stats Grid */}
            <div className="grid grid-cols-2 gap-4 w-full max-w-md">
              <div className={`backdrop-blur-sm rounded-2xl p-4 shadow-lg border flex flex-col justify-center ${
                darkMode 
                  ? 'bg-gray-800/70 border-gray-700/50 text-gray-200' 
                  : 'bg-white/80 border-white/20 text-gray-800'
              }`}>
                <div className="text-3xl font-bold">{totalCount}</div>
                <div className="text-sm opacity-80">Total Tasks</div>
              </div>
              <div className={`backdrop-blur-sm rounded-2xl p-4 shadow-lg border flex flex-col justify-center ${
                darkMode 
                  ? 'bg-gray-800/70 border-gray-700/50 text-emerald-300' 
                  : 'bg-white/80 border-white/20 text-emerald-600'
              }`}>
                <div className="text-3xl font-bold">{completedCount}</div>
                <div className="text-sm opacity-80">Completed</div>
              </div>
              <div className={`backdrop-blur-sm rounded-2xl p-4 shadow-lg border flex flex-col justify-center ${
                darkMode 
                  ? 'bg-gray-800/70 border-gray-700/50 text-amber-300' 
                  : 'bg-white/80 border-white/20 text-amber-600'
              }`}>
                <div className="text-3xl font-bold">{pendingCount}</div>
                <div className="text-sm opacity-80">Pending</div>
              </div>
              <div className={`backdrop-blur-sm rounded-2xl p-4 shadow-lg border flex flex-col justify-center animate-pulse ${
                darkMode 
                  ? 'bg-gray-800/70 border-gray-700/50 text-rose-300' 
                  : 'bg-white/80 border-white/20 text-rose-600'
              }`}>
                <div className="text-3xl font-bold">{overdueCount}</div>
                <div className="text-sm opacity-80">Overdue</div>
              </div>
            </div>

            {/* Right: Enhanced Modern Circular Progress Chart */}
            <div className="relative w-72 h-72 flex items-center justify-center">
              {/* Outer glow effect */}
              <div className={`absolute inset-0 rounded-full blur-xl opacity-30 ${
                darkMode 
                  ? 'bg-gradient-to-r from-blue-600 to-purple-600' 
                  : 'bg-gradient-to-r from-blue-500 to-purple-500'
              }`}></div>
              
              {/* Main container with backdrop */}
              <div className={`relative w-64 h-64 rounded-full backdrop-blur-sm border shadow-2xl ${
                darkMode 
                  ? 'bg-gray-800/40 border-gray-700/30' 
                  : 'bg-white/40 border-white/30'
              }`}>
                <svg className="w-full h-full" viewBox="0 0 100 100">
                  {/* Decorative outer ring */}
                  <circle
                    cx="50"
                    cy="50"
                    r="45"
                    fill="none"
                    stroke={darkMode ? "rgba(55, 65, 81, 0.1)" : "rgba(229, 231, 235, 0.3)"}
                    strokeWidth="0.5"
                    strokeDasharray="2 2"
                  />
                  
                  {/* Background Circle */}
                  <circle
                    cx="50"
                    cy="50"
                    r="38"
                    fill="none"
                    stroke={darkMode ? "rgba(55, 65, 81, 0.2)" : "rgba(229, 231, 235, 0.4)"}
                    strokeWidth="8"
                  />
                  
                  {/* Progress Track */}
                  <circle
                    cx="50"
                    cy="50"
                    r="38"
                    fill="none"
                    stroke={darkMode ? "rgba(55, 65, 81, 0.3)" : "rgba(209, 213, 219, 0.4)"}
                    strokeWidth="8"
                    strokeDasharray="239"
                    strokeDashoffset="0"
                  />
                  
                  {/* Progress Bar with enhanced gradient */}
                  <circle
                    cx="50"
                    cy="50"
                    r="38"
                    fill="none"
                    strokeLinecap="round"
                    stroke={darkMode ? "url(#enhanced-progress-gradient-dark)" : "url(#enhanced-progress-gradient-light)"}
                    strokeWidth="8"
                    strokeDasharray="239"
                    strokeDashoffset={239 * (1 - progressPercentage / 100)}
                    transform="rotate(-90 50 50)"
                    style={{ 
                      transition: 'stroke-dashoffset 1.2s cubic-bezier(0.4, 0, 0.2, 1)',
                      filter: 'drop-shadow(0 0 8px rgba(59, 130, 246, 0.5))'
                    }}
                  />
                  
                  {/* Animated progress dot */}
                  {progressPercentage > 0 && (
                    <circle
                      cx={50 + 38 * Math.cos((progressPercentage / 100) * 2 * Math.PI - Math.PI / 2)}
                      cy={50 + 38 * Math.sin((progressPercentage / 100) * 2 * Math.PI - Math.PI / 2)}
                      r="4"
                      fill={darkMode ? "#60a5fa" : "#3b82f6"}
                      style={{ 
                        transition: 'all 1.2s cubic-bezier(0.4, 0, 0.2, 1)',
                        filter: 'drop-shadow(0 0 6px rgba(59, 130, 246, 0.8))'
                      }}
                    >
                      <animate
                        attributeName="r"
                        values="3;5;3"
                        dur="2s"
                        repeatCount="indefinite"
                      />
                    </circle>
                  )}
                  
                  {/* Enhanced Gradient Definitions */}
                  <defs>
                    <linearGradient id="enhanced-progress-gradient-light" x1="0%" y1="0%" x2="100%" y2="0%">
                      <stop offset="0%" stopColor="#3b82f6" />
                      <stop offset="50%" stopColor="#6366f1" />
                      <stop offset="100%" stopColor="#8b5cf6" />
                    </linearGradient>
                    <linearGradient id="enhanced-progress-gradient-dark" x1="0%" y1="0%" x2="100%" y2="0%">
                      <stop offset="0%" stopColor="#60a5fa" />
                      <stop offset="50%" stopColor="#818cf8" />
                      <stop offset="100%" stopColor="#a78bfa" />
                    </linearGradient>
                    
                    {/* Radial gradient for center glow */}
                    <radialGradient id="center-glow-light" cx="50%" cy="50%" r="50%">
                      <stop offset="0%" stopColor="rgba(59, 130, 246, 0.1)" />
                      <stop offset="100%" stopColor="rgba(59, 130, 246, 0)" />
                    </radialGradient>
                    <radialGradient id="center-glow-dark" cx="50%" cy="50%" r="50%">
                      <stop offset="0%" stopColor="rgba(96, 165, 250, 0.1)" />
                      <stop offset="100%" stopColor="rgba(96, 165, 250, 0)" />
                    </radialGradient>
                  </defs>
                  
                  {/* Center glow effect */}
                  <circle
                    cx="50"
                    cy="50"
                    r="25"
                    fill={darkMode ? "url(#center-glow-dark)" : "url(#center-glow-light)"}
                  />
                </svg>
                
                {/* Center content with enhanced styling */}
                <div className="absolute inset-0 flex flex-col items-center justify-center">
                  <div className={`text-4xl font-bold mb-1 bg-clip-text text-transparent ${
                    darkMode 
                      ? 'bg-gradient-to-r from-blue-300 to-purple-300' 
                      : 'bg-gradient-to-r from-blue-600 to-purple-600'
                  }`}>
                    {Math.round(progressPercentage)}%
                  </div>
                  <div className={`text-xs font-medium tracking-widest uppercase ${
                    darkMode ? 'text-gray-400' : 'text-gray-500'
                  }`}>
                    Completion Rate
                  </div>
                  
                  {/* Achievement indicator for 100% */}
                  {progressPercentage === 100 && (
                    <div className={`mt-2 px-3 py-1 rounded-full text-xs font-medium animate-bounce ${
                      darkMode 
                        ? 'bg-emerald-900/30 text-emerald-300 border border-emerald-700/50' 
                        : 'bg-emerald-100 text-emerald-700 border border-emerald-200'
                    }`}>
                      ðŸŽ‰ All Done!
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Add Task Form */}
        <div className={`mb-10 shadow-2xl overflow-hidden rounded-2xl ${
          darkMode 
            ? 'bg-gray-800/80 backdrop-blur-sm border border-gray-700/50' 
            : 'bg-white/90 backdrop-blur-sm border-0'
        }`}>
          <div className={`absolute inset-0 ${
            darkMode 
              ? 'bg-gradient-to-r from-blue-700/5 to-purple-700/5' 
              : 'bg-gradient-to-r from-blue-600/5 to-purple-600/5'
          }`}></div>
          <div className="relative p-6">
            <h2 className={`text-2xl flex items-center gap-3 font-bold mb-6 ${
              darkMode ? 'text-gray-200' : 'text-gray-800'
            }`}>
              <div className={`w-8 h-8 rounded-lg flex items-center justify-center ${
                darkMode 
                  ? 'bg-gradient-to-r from-blue-600 to-purple-600' 
                  : 'bg-gradient-to-r from-blue-500 to-purple-500'
              }`}>
                <PlusIcon className="h-4 w-4 text-white" />
              </div>
              Create New Task
            </h2>
            <div className="relative space-y-6">
              <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
                <div className="lg:col-span-2">
                  <input
                    placeholder="What needs to be done?"
                    value={newTask.title}
                    onChange={(e) => setNewTask({...newTask, title: e.target.value})}
                    className={`w-full h-12 text-lg rounded-lg px-4 focus:outline-none focus:ring-2 ${
                      darkMode 
                        ? 'bg-gray-700/50 border-gray-600 focus:ring-blue-500/30 focus:border-blue-500 text-gray-200' 
                        : 'bg-white/70 border-gray-200 focus:ring-blue-400/20 focus:border-blue-400 text-gray-800'
                    }`}
                    onKeyPress={(e) => e.key === 'Enter' && addTask()}
                  />
                </div>
                <div>
                  <select
                    value={newTask.priority}
                    onChange={(e) => setNewTask({...newTask, priority: e.target.value as Task['priority']})}
                    className={`w-full h-12 px-4 text-lg rounded-lg focus:outline-none focus:ring-2 ${
                      darkMode 
                        ? 'bg-gray-700/50 border-gray-600 focus:ring-blue-500/30 focus:border-blue-500 text-gray-200' 
                        : 'bg-white/70 border-gray-200 focus:ring-blue-400/20 focus:border-blue-400 text-gray-800'
                    }`}
                  >
                    <option value="low">ðŸŸ¢ Low Priority</option>
                    <option value="medium">ðŸŸ¡ Medium Priority</option>
                    <option value="high">ðŸ”´ High Priority</option>
                  </select>
                </div>
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <input
                  placeholder="Add a description (optional)..."
                  value={newTask.description}
                  onChange={(e) => setNewTask({...newTask, description: e.target.value})}
                  className={`w-full h-12 text-lg rounded-lg px-4 focus:outline-none focus:ring-2 ${
                    darkMode 
                      ? 'bg-gray-700/50 border-gray-600 focus:ring-blue-500/30 focus:border-blue-500 text-gray-200' 
                      : 'bg-white/70 border-gray-200 focus:ring-blue-400/20 focus:border-blue-400 text-gray-800'
                  }`}
                />
                <input
                  type="date"
                  value={newTask.dueDate}
                  onChange={(e) => setNewTask({...newTask, dueDate: e.target.value})}
                  className={`w-full h-12 text-lg rounded-lg px-4 focus:outline-none focus:ring-2 ${
                    darkMode 
                      ? 'bg-gray-700/50 border-gray-600 focus:ring-blue-500/30 focus:border-blue-500 text-gray-200' 
                      : 'bg-white/70 border-gray-200 focus:ring-blue-400/20 focus:border-blue-400 text-gray-800'
                  }`}
                />
              </div>
              <button 
                onClick={addTask}
                disabled={!newTask.title.trim()}
                className={`w-full h-12 text-lg text-white shadow-lg hover:shadow-xl transition-all duration-200 rounded-lg
                  ${!newTask.title.trim() 
                    ? 'bg-gray-500 cursor-not-allowed' 
                    : darkMode
                      ? 'bg-gradient-to-r from-blue-700 to-purple-700 hover:from-blue-600 hover:to-purple-600 hover:scale-[1.02]'
                      : 'bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 hover:scale-[1.02]'} 
                  flex items-center justify-center`}
              >
                <PlusIcon className="h-5 w-5 mr-2" />
                Add Task
              </button>
            </div>
          </div>
        </div>

        {/* Filter and Search */}
        <div className="mb-8 flex flex-col lg:flex-row gap-6 items-center justify-between">
          <div className={`flex gap-2 backdrop-blur-sm rounded-2xl p-2 shadow-lg ${
            darkMode ? 'bg-gray-800/70' : 'bg-white/80'
          }`}>
            <button
              onClick={() => setFilter('all')}
              className={`rounded-xl transition-all duration-200 px-4 py-2 ${
                filter === 'all' 
                  ? darkMode 
                    ? 'bg-gradient-to-r from-blue-700 to-purple-700 text-white' 
                    : 'bg-gradient-to-r from-blue-600 to-purple-600 text-white'
                  : darkMode 
                    ? 'hover:bg-gray-700 text-gray-300' 
                    : 'hover:bg-gray-100'
              }`}
            >
              All <span className={`ml-2 rounded-full px-2 py-0.5 ${
                darkMode ? 'bg-gray-900/50' : 'bg-white/20'
              }`}>{totalCount}</span>
            </button>
            <button
              onClick={() => setFilter('pending')}
              className={`rounded-xl transition-all duration-200 px-4 py-2 ${
                filter === 'pending' 
                  ? darkMode
                   ? 'bg-gradient-to-r from-amber-700 to-orange-700 text-white' 
                    : 'bg-gradient-to-r from-amber-600 to-orange-600 text-white'
                  : darkMode 
                    ? 'hover:bg-gray-700 text-gray-300' 
                    : 'hover:bg-gray-100'
              }`}
            >
              Pending <span className={`ml-2 rounded-full px-2 py-0.5 ${
                darkMode ? 'bg-gray-900/50' : 'bg-white/20'
              }`}>{pendingCount}</span>
            </button>
            <button
              onClick={() => setFilter('completed')}
              className={`rounded-xl transition-all duration-200 px-4 py-2 ${
                filter === 'completed' 
                  ? darkMode 
                    ? 'bg-gradient-to-r from-emerald-700 to-teal-700 text-white' 
                    : 'bg-gradient-to-r from-emerald-600 to-teal-600 text-white'
                  : darkMode 
                    ? 'hover:bg-gray-700 text-gray-300' 
                    : 'hover:bg-gray-100'
              }`}
            >
              Completed <span className={`ml-2 rounded-full px-2 py-0.5 ${
                darkMode ? 'bg-gray-900/50' : 'bg-white/20'
              }`}>{completedCount}</span>
            </button>
            <button
              onClick={() => setFilter('overdue')}
              className={`rounded-xl transition-all duration-200 px-4 py-2 ${
                filter === 'overdue' 
                  ? darkMode 
                    ? 'bg-gradient-to-r from-rose-700 to-pink-700 text-white' 
                    : 'bg-gradient-to-r from-rose-600 to-pink-600 text-white'
                  : darkMode 
                    ? 'hover:bg-gray-700 text-gray-300' 
                    : 'hover:bg-gray-100'
              }`}
            >
              Overdue <span className={`ml-2 rounded-full px-2 py-0.5 ${
                darkMode ? 'bg-gray-900/50' : 'bg-white/20'
              }`}>{overdueCount}</span>
            </button>
          </div>

          <div className={`relative w-full lg:w-auto backdrop-blur-sm rounded-2xl shadow-lg ${
            darkMode ? 'bg-gray-800/70' : 'bg-white/80'
          }`}>
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <SearchIcon className={`h-5 w-5 ${darkMode ? 'text-gray-400' : 'text-gray-500'}`} />
            </div>
            <input
              type="text"
              placeholder="Search tasks..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className={`w-full h-12 pl-10 pr-4 rounded-2xl focus:outline-none focus:ring-2 ${
                darkMode 
                  ? 'bg-gray-700/50 border-gray-600 focus:ring-blue-500/30 focus:border-blue-500 text-gray-200' 
                  : 'bg-white/70 border-gray-200 focus:ring-blue-400/20 focus:border-blue-400 text-gray-800'
              }`}
            />
          </div>
        </div>

        {/* Tasks List */}
        <div className="space-y-4 mb-20">
          {filteredTasks.length === 0 ? (
            <div className={`text-center py-16 rounded-2xl shadow-lg ${
              darkMode 
                ? 'bg-gray-800/70 border border-gray-700/50' 
                : 'bg-white/90 border-0'
            }`}>
              <div className="flex flex-col items-center justify-center">
                <SparklesIcon className={`h-12 w-12 mb-4 ${
                  darkMode ? 'text-purple-400' : 'text-purple-600'
                }`} />
                <h3 className={`text-xl font-bold mb-2 ${
                  darkMode ? 'text-gray-200' : 'text-gray-800'
                }`}>
                  No tasks found
                </h3>
                <p className={`max-w-md mx-auto ${
                  darkMode ? 'text-gray-400' : 'text-gray-600'
                }`}>
                  {searchTerm 
                    ? 'Try adjusting your search query'
                    : filter === 'completed'
                      ? 'You haven\'t completed any tasks yet'
                      : filter === 'overdue'
                        ? 'No overdue tasks - great job!'
                        : 'Add your first task to get started'}
                </p>
              </div>
            </div>
          ) : (
            filteredTasks.map((task) => (
              <div
                key={task.id}
                draggable
                onDragStart={(e) => handleDragStart(e, task.id)}
                onDragOver={(e) => handleDragOver(e, task.id)}
                onDragLeave={handleDragLeave}
                onDrop={(e) => handleDrop(e, task.id)}
                onDragEnd={handleDragEnd}
                className={`relative transition-all duration-200 ${
                  draggedTaskId === task.id ? 'opacity-50' : 'opacity-100'
                } ${
                  dragOverTaskId === task.id 
                    ? darkMode 
                      ? 'border-l-4 border-purple-500' 
                      : 'border-l-4 border-blue-500'
                    : ''
                }`}
              >
                {/* Task Card */}
                <div className={`group rounded-2xl shadow-lg overflow-hidden transition-all duration-200 hover:shadow-xl ${
                  darkMode 
                    ? 'bg-gray-800/70 border border-gray-700/50 hover:border-gray-600' 
                    : 'bg-white/90 border-0 hover:border-gray-200'
                }`}>
                  <div className="absolute inset-0 w-1.5 bg-gradient-to-b from-blue-500 to-purple-500"></div>
                  
                  <div className="relative pl-6 pr-4 py-4">
                    {/* Drag handle */}
                    <div className={`absolute left-1 top-1/2 transform -translate-y-1/2 p-1 rounded-md cursor-move opacity-0 group-hover:opacity-100 transition-opacity ${
                      darkMode ? 'hover:bg-gray-700' : 'hover:bg-gray-100'
                    }`}>
                      <DragHandleIcon className={`h-4 w-4 ${darkMode ? 'text-gray-400' : 'text-gray-500'}`} />
                    </div>

                    {/* Task header */}
                    <div className="flex items-start justify-between mb-2">
                      <div className="flex items-center gap-3">
                        <button
                          onClick={() => toggleComplete(task.id)}
                          className={`flex-shrink-0 w-6 h-6 rounded-full flex items-center justify-center transition-all ${
                            task.completed
                              ? darkMode
                                ? 'bg-emerald-500/90 text-white'
                                : 'bg-emerald-500 text-white'
                              : darkMode
                                ? 'border-2 border-gray-500 hover:border-emerald-400'
                                : 'border-2 border-gray-300 hover:border-emerald-400'
                          }`}
                        >
                          {task.completed && <CheckIcon className="h-4 w-4" />}
                        </button>
                        <h3 className={`text-lg font-medium ${
                          task.completed
                            ? darkMode
                              ? 'line-through text-gray-500'
                              : 'line-through text-gray-400'
                            : darkMode
                              ? 'text-gray-200'
                              : 'text-gray-800'
                        }`}>
                          {task.title}
                        </h3>
                      </div>
                      <div className="flex gap-2">
                        <button
                          onClick={() => startEdit(task)}
                          className={`p-1.5 rounded-lg transition-colors ${
                            darkMode
                              ? 'hover:bg-gray-700 text-gray-300 hover:text-blue-400'
                              : 'hover:bg-gray-100 text-gray-500 hover:text-blue-600'
                          }`}
                        >
                          <EditIcon className="h-5 w-5" />
                        </button>
                        <button
                          onClick={() => deleteTask(task.id)}
                          className={`p-1.5 rounded-lg transition-colors ${
                            darkMode
                              ? 'hover:bg-gray-700 text-gray-300 hover:text-rose-400'
                              : 'hover:bg-gray-100 text-gray-500 hover:text-rose-600'
                          }`}
                        >
                          <TrashIcon className="h-5 w-5" />
                        </button>
                      </div>
                    </div>

                    {/* Task body */}
                    {task.description && (
                      <p className={`ml-9 mb-3 ${
                        task.completed
                          ? darkMode
                            ? 'line-through text-gray-500'
                            : 'line-through text-gray-400'
                          : darkMode
                            ? 'text-gray-400'
                            : 'text-gray-600'
                      }`}>
                        {task.description}
                      </p>
                    )}

                    {/* Task footer */}
                    <div className="ml-9 flex flex-wrap items-center gap-3">
                      {/* Priority */}
                      <div className={`flex items-center gap-1.5 px-3 py-1 rounded-full text-sm border ${
                        priorityConfig[task.priority].color
                      }`}>
                        {task.priority === 'low' && <priorityConfig.low.icon className="h-3.5 w-3.5" />}
                        {task.priority === 'medium' && <priorityConfig.medium.icon className="h-3.5 w-3.5" />}
                        {task.priority === 'high' && <priorityConfig.high.icon className="h-3.5 w-3.5" />}
                        {task.priority.charAt(0).toUpperCase() + task.priority.slice(1)}
                      </div>

                      {/* Due date */}
                      {task.dueDate && (
                        <div className={`flex items-center gap-1.5 px-3 py-1 rounded-full text-sm ${
                          new Date(task.dueDate) < new Date() && !task.completed
                            ? darkMode
                              ? 'bg-rose-900/30 text-rose-300 border border-rose-800/50'
                              : 'bg-rose-100 text-rose-700 border border-rose-200'
                            : darkMode
                              ? 'bg-gray-700/50 text-gray-300 border border-gray-600/50'
                              : 'bg-gray-100 text-gray-600 border border-gray-200'
                        }`}>
                          <CalendarIcon className="h-3.5 w-3.5" />
                          {new Date(task.dueDate).toLocaleDateString('en-US', {
                            month: 'short',
                            day: 'numeric',
                            year: 'numeric'
                          })}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            ))
          )}
        </div>

        {/* Edit Task Modal */}
        {editingTask && (
          <div className={`fixed inset-0 z-50 flex items-center justify-center p-4 ${
            darkMode ? 'bg-black/80' : 'bg-black/50'
          }`}>
            <div className={`relative w-full max-w-2xl rounded-2xl shadow-2xl overflow-hidden ${
              darkMode 
                ? 'bg-gray-800 border border-gray-700/50' 
                : 'bg-white border-0'
            }`}>
              <div className={`absolute inset-0 ${
                darkMode 
                  ? 'bg-gradient-to-r from-blue-900/10 to-purple-900/10' 
                  : 'bg-gradient-to-r from-blue-600/5 to-purple-600/5'
              }`}></div>
              <div className="relative p-6">
                <div className="flex items-center justify-between mb-6">
                  <h2 className={`text-2xl font-bold ${
                    darkMode ? 'text-gray-200' : 'text-gray-800'
                  }`}>
                    Edit Task
                  </h2>
                  <button
                    onClick={cancelEdit}
                    className={`p-2 rounded-full transition-colors ${
                      darkMode
                        ? 'hover:bg-gray-700 text-gray-300 hover:text-rose-400'
                        : 'hover:bg-gray-100 text-gray-500 hover:text-rose-600'
                    }`}
                  >
                    <CloseIcon className="h-6 w-6" />
                  </button>
                </div>

                <div className="space-y-6">
                  <div>
                    <label className={`block mb-2 font-medium ${
                      darkMode ? 'text-gray-300' : 'text-gray-700'
                    }`}>
                      Title
                    </label>
                    <input
                      value={editingTask.title}
                      onChange={(e) => setEditingTask({...editingTask, title: e.target.value})}
                      className={`w-full h-12 text-lg rounded-lg px-4 focus:outline-none focus:ring-2 ${
                        darkMode 
                          ? 'bg-gray-700/50 border-gray-600 focus:ring-blue-500/30 focus:border-blue-500 text-gray-200' 
                          : 'bg-white/70 border-gray-200 focus:ring-blue-400/20 focus:border-blue-400 text-gray-800'
                      }`}
                    />
                  </div>

                  <div>
                    <label className={`block mb-2 font-medium ${
                      darkMode ? 'text-gray-300' : 'text-gray-700'
                    }`}>
                      Description
                    </label>
                    <textarea
                      value={editingTask.description}
                      onChange={(e) => setEditingTask({...editingTask, description: e.target.value})}
                      rows={3}
                      className={`w-full text-lg rounded-lg px-4 py-3 focus:outline-none focus:ring-2 ${
                        darkMode 
                          ? 'bg-gray-700/50 border-gray-600 focus:ring-blue-500/30 focus:border-blue-500 text-gray-200' 
                          : 'bg-white/70 border-gray-200 focus:ring-blue-400/20 focus:border-blue-400 text-gray-800'
                      }`}
                    />
                  </div>

                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <label className={`block mb-2 font-medium ${
                        darkMode ? 'text-gray-300' : 'text-gray-700'
                      }`}>
                        Priority
                      </label>
                      <select
                        value={editingTask.priority}
                        onChange={(e) => setEditingTask({...editingTask, priority: e.target.value as Task['priority']})}
                        className={`w-full h-12 px-4 text-lg rounded-lg focus:outline-none focus:ring-2 ${
                          darkMode 
                            ? 'bg-gray-700/50 border-gray-600 focus:ring-blue-500/30 focus:border-blue-500 text-gray-200' 
                            : 'bg-white/70 border-gray-200 focus:ring-blue-400/20 focus:border-blue-400 text-gray-800'
                        }`}
                      >
                        <option value="low">Low</option>
                        <option value="medium">Medium</option>
                        <option value="high">High</option>
                      </select>
                    </div>

                    <div>
                      <label className={`block mb-2 font-medium ${
                        darkMode ? 'text-gray-300' : 'text-gray-700'
                      }`}>
                        Due Date
                      </label>
                      <input
                        type="date"
                        value={editingTask.dueDate || ''}
                        onChange={(e) => setEditingTask({...editingTask, dueDate: e.target.value})}
                        className={`w-full h-12 text-lg rounded-lg px-4 focus:outline-none focus:ring-2 ${
                          darkMode 
                            ? 'bg-gray-700/50 border-gray-600 focus:ring-blue-500/30 focus:border-blue-500 text-gray-200' 
                            : 'bg-white/70 border-gray-200 focus:ring-blue-400/20 focus:border-blue-400 text-gray-800'
                        }`}
                      />
                    </div>
                  </div>

                  <div className="flex justify-end gap-3 pt-4">
                    <button
                      onClick={cancelEdit}
                      className={`px-6 py-3 rounded-lg font-medium transition-colors ${
                        darkMode
                          ? 'bg-gray-700 hover:bg-gray-600 text-gray-200'
                          : 'bg-gray-200 hover:bg-gray-300 text-gray-800'
                      }`}
                    >
                      Cancel
                    </button>
                    <button
                      onClick={saveEdit}
                      className={`px-6 py-3 rounded-lg font-medium text-white transition-colors ${
                        darkMode
                          ? 'bg-gradient-to-r from-blue-700 to-purple-700 hover:from-blue-600 hover:to-purple-600'
                          : 'bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700'
                      }`}
                    >
                      Save Changes
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Footer with animated icons and drag & drop info */}
        <div className={`fixed bottom-0 left-0 right-0 backdrop-blur-sm border-t ${
          darkMode 
            ? 'bg-gray-900/80 border-gray-700/50' 
            : 'bg-white/80 border-gray-200/50'
        }`}>
          <div className="container mx-auto px-4 py-2 max-w-5xl">
            <div className="flex flex-col md:flex-row items-center justify-center text-center gap-6">

              {/* Left: Animated icons */}
              <div className="flex items-center gap-6">
                <div className="flex items-center gap-2">
                  <MousePointerIcon className={`h-5 w-5 animate-pulse ${
                    darkMode ? 'text-blue-400' : 'text-blue-600'
                  }`} />
                  <span className={`text-sm ${
                    darkMode ? 'text-gray-300' : 'text-gray-600'
                  }`}>
                    Click to interact
                  </span>
                </div>
                <div className="flex items-center gap-2">
                  <MoveIcon className={`h-5 w-5 animate-bounce ${
                    darkMode ? 'text-purple-400' : 'text-purple-600'
                  }`} />
                  <span className={`text-sm ${
                    darkMode ? 'text-gray-300' : 'text-gray-600'
                  }`}>
                    Drag to reorder
                  </span>
                </div>
                <div className="flex items-center gap-2">
                  <CheckCircleIcon className={`h-5 w-5 animate-pulse ${
                    darkMode ? 'text-emerald-400' : 'text-emerald-600'
                  }`} />
                  <span className={`text-sm ${
                    darkMode ? 'text-gray-300' : 'text-gray-600'
                  }`}>
                    Mark complete
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}